#calendar-body {
    margin: 0;
    padding: 20px;
    height: 90vh; /* Altezza massima relativa alla viewport */
    overflow: hidden; /* Evita lo scrollbar del body */
    font-family: Arial, sans-serif;
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
}

#calendar {
    display: flex;
    overflow-x: auto;
    gap: 10px;
    flex-grow: 1;
    margin-bottom: 0;
    box-sizing: border-box;
    height: 100%; /* Garantisce che il calendario riempia il suo contenitore */
}

#calendar::-webkit-scrollbar {
    display: none; /* Nasconde scrollbar nei browser WebKit */
}

.day-column {
    background-color: white;
    border-radius: 5px;
    padding: 0 10px 10px 10px; /* Padding per i contenuti */
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    min-width: 150px;
    flex-shrink: 0;
    position: relative; /* Necessario per sticky positioning */
    display: flex;
    flex-direction: column;
}

.day-column::-webkit-scrollbar {
    display: none; /* Nasconde scrollbar nei browser WebKit */
}

.day-header {
    font-weight: bold;
    text-align: center;
    position: sticky;
    top: 0;
    background-color: white;
    padding: 10px;
    z-index: 1;
    margin: 0;
}

.pills-container {
    flex-grow: 1;
    overflow-y: scroll; /* Abilita lo scrolling verticale delle pills */
    max-height: calc(100% - 50px); /* Assicurati che le pills abbiano un'area di scroll */
    scrollbar-width: none; /* Firefox: Nasconde scrollbar */
    -ms-overflow-style: none;  /* IE e Edge: Nasconde scrollbar */
}

.pills-container::-webkit-scrollbar {
    display: none; /* WebKit Browsers: Nasconde scrollbar */
}

.hour-pill {
    padding: 5px;
    margin: 5px 0;
    text-align: center;
    border-radius: 25px;
    cursor: pointer;
}

.hour-pill.locked {
    background-color: #ff4d4d;
    color: white;
}

.hour-pill.unlocked {
    background-color: #4caf50;
    color: white;
}

.hour-pill.hidden {
    display: none;
}

#calendar {
    scroll-behavior: smooth;
}
