@import url(https://fonts.googleapis.com/css?family=Text+Me+One);





.left {
    background-color:#F0F0F0;
    float:right;
    height:100%;
    min-width:256px;
    position:fixed;
    width:35%;
}

.name-hero {
    bottom:0;
    height:290px;
    left:0;
    margin:auto;
    position:absolute;
    right:0;
    top:0;
    width:85%;
}

.circle {
    width: 200px;
    height: 200px;
    border-radius: 40% 60% 70% 30% / 40% 50% 60% 50%;
    overflow: hidden;
    animation: morph 3s linear infinite;
    background-size:100%;
    background-position:0px;
    border-radius:100%;
    height:150px;
    margin:0 auto;
    position:relative;
    width:150px;
}

.circle img {
    width: 100%;
}

@keyframes morph {
    0%, 100% {
        border-radius: 40% 60% 70% 30% / 40% 40% 60% 50%;
    }
    34% {
        border-radius: 70% 30% 50% 50% / 30% 30% 70% 70%;
    }
    67% {
        border-radius: 100% 60% 60% 100% / 100% 100% 60% 60%;
    }
}

.skill-set li
{
    border-radius:5px;
    color:#FFF;
    display:inline-block;
    list-style:none;
    margin:15px 15px 0 0;
    padding:10px;
    text-align:justify;
    background: linear-gradient(to right, #d09595 70%, #d09595 30%);
}

.right
{
    float:right;
    height:100%;
    position:relative;
    width:65%;
    background-color:#FFF;
}






.name-hero h1
{
    font-size:1.5em;
    text-align:center;
}

.name-hero h1 em
{
    color:rgba(0,0,0,.3);
    font-style:normal;
    font-weight:700;
}

.name-hero p
{
    color:rgba(0,0,0,.75);
    font-size:.75em;
    line-height:1.5;
    margin:0 8px 0 0;
    text-align:center;
}

.name-hero .name-text
{
    margin:0 auto;
    width:85%;
}

.inner
{
    margin:0 auto;
    max-width:975px;
    padding:3em;
}

.inner h1
{
    font-size:1.75em;
}

.inner p
{
    color:rgba(0,0,0,.5);
}

.inner p em
{
    color:rgba(0,0,0,1);
    font-style:normal;
}

.inner section
{
    margin:100px auto;
}

ul
{
    list-style-type:none;
    margin-top:-10px;
    max-width:570px;
    padding:0;
}



@media screen and (max-width: 48em) {
    .right,.left
    {
        float:none;
        position:relative !important;
        width:100%;
        min-height:500px;
    }


    @media screen and (max-width: 75em) {
        body
        { font-size:16px;}
    }

    @media screen and (max-width: 60em) {
        body
        { font-size:14px;}
    }

}

#calendar-body {
    margin: 0;
    padding: 20px;
    height: 90vh; /* Altezza massima relativa alla viewport */
    overflow: hidden; /* Evita lo scrollbar del body */
    font-family: Arial, sans-serif;
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
}

#calendar {
    display: flex;
    overflow-x: auto;
    grid-gap: 10px;
    gap: 10px;
    flex-grow: 1;
    margin-bottom: 0;
    box-sizing: border-box;
    height: 100%; /* Garantisce che il calendario riempia il suo contenitore */
}

#calendar::-webkit-scrollbar {
    display: none; /* Nasconde scrollbar nei browser WebKit */
}

.day-column {
    background-color: white;
    border-radius: 5px;
    padding: 0 10px 10px 10px; /* Padding per i contenuti */
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    min-width: 150px;
    flex-shrink: 0;
    position: relative; /* Necessario per sticky positioning */
    display: flex;
    flex-direction: column;
}

.day-column::-webkit-scrollbar {
    display: none; /* Nasconde scrollbar nei browser WebKit */
}

.day-header {
    font-weight: bold;
    text-align: center;
    position: sticky;
    top: 0;
    background-color: white;
    padding: 10px;
    z-index: 1;
    margin: 0;
}

.pills-container {
    flex-grow: 1;
    overflow-y: scroll; /* Abilita lo scrolling verticale delle pills */
    max-height: calc(100% - 50px); /* Assicurati che le pills abbiano un'area di scroll */
    scrollbar-width: none; /* Firefox: Nasconde scrollbar */
    -ms-overflow-style: none;  /* IE e Edge: Nasconde scrollbar */
}

.pills-container::-webkit-scrollbar {
    display: none; /* WebKit Browsers: Nasconde scrollbar */
}

.hour-pill {
    padding: 5px;
    margin: 5px 0;
    text-align: center;
    border-radius: 25px;
    cursor: pointer;
}

.hour-pill.locked {
    background-color: #ff4d4d;
    color: white;
}

.hour-pill.unlocked {
    background-color: #4caf50;
    color: white;
}

.hour-pill.hidden {
    display: none;
}

#calendar {
    scroll-behavior: smooth;
}

*, *:before, *:after { box-sizing: border-box;
}

html {
    font-size:100%;
}

body {
    font-family: 'Text Me One', sans-serif;
    -webkit-font-smoothing:antialiased;
    font-size:18px;
    font-weight:400;
    line-height:1.4;
    text-rendering:optimizeLegibility;
    margin: 0;
    background-color: #f4f4f4;
}

